:root {
  --white: #f9f9f9;
  --black: #36383F;
  --grey: #85888C;
}

/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 2000px;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

/* Header */
.nav {
  top: 0;
  position: relative;
  width: fit-content;
  background-color: transparent;
  z-index: 1;
}

.header__nav-list {
  display: flex;
}

.header__nav-list a {
  display: block;
  color: var(--white);
  transition: all 0.3s ease;
}

.header__nav-list a:hover {
  /* background-color: transparent; */
}

.hamb {
  display: none;
}

.mobile-menu {
  display: none;
}

@media (max-width: 1050px) {
  .header {
    /* position: sticky; */
  }
  /* Nav menu */
  .nav {
    overflow: hidden;
    max-height: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    background-color: #222222;
    padding-left: 26px;
    transition: max-height 0.5s ease-out;
  }
  .header__nav-list {
    flex-direction: column;
    align-items: start;
    margin-top: 50px;
  }
  .header__nav-link {
    display: inline-block;
    float: left;
  }
  .header__nav-list a:hover {
    /* background-color: var(--grey); */
  }
  /* Menu Icon */
  .hamb {
    position: relative;
    display: block;
    cursor: pointer;
    float: right;
    padding: 56px 20px;
    z-index: 2;
  }
  .hamb-line {
    background: var(--white);
    display: block;
    height: 2px;
    position: relative;
    width: 18px;
  }
  .hamb-line::before,
  .hamb-line::after {
    background: var(--white);
    content: "";
    display: block;
    height: 100%;
    position: absolute;
    transition: all 0.2s ease-out;
    width: 24px;
    right: 0;
  }
  .hamb-line::before {
    top: 8px;
  }
  .hamb-line::after {
    top: -8px;
  }
  /* Toggle menu icon */
  .mobile-menu:checked ~ nav {
    max-height: 100%;
  }
  .mobile-menu:checked ~ .hamb .hamb-line {
    background: transparent;
    position: fixed;
    right: 40px;
  }
  .mobile-menu:checked ~ .hamb .hamb-line::before {
    transform: rotate(-45deg);
    top: 0;
  }
  .mobile-menu:checked ~ .hamb .hamb-line::after {
    transform: rotate(45deg);
    top: 0;
  }
}
body {
  /* font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif; */
  font-family: Nunito Sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  color: #292b2c;
  background-color: #fff;
  /* height: 2000px; */
}

/* свойства модального окна по умолчанию */
.modal {
  position: fixed; /* фиксированное положение */
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  /* background: rgba(0,0,0,0.5); цвет фона */
  background: rgba(255, 255, 255, 0.5); /* цвет фона */
  z-index: 1050;
  opacity: 0; /* по умолчанию модальное окно прозрачно */
  -webkit-transition: opacity 200ms ease-in;
  -moz-transition: opacity 200ms ease-in;
  transition: opacity 200ms ease-in; /* анимация перехода */
  pointer-events: none; /* элемент невидим для событий мыши */
  margin: 0;
  padding: 0;
}

/* при отображении модального окно */
.modal:target {
  opacity: 1; /* делаем окно видимым */
  pointer-events: auto; /* элемент видим для событий мыши */
  overflow-y: auto; /* добавляем прокрутку по y, когда элемент не помещается на страницу */
}

/* ширина модального окна и его отступы от экрана */
.modal-dialog {
  position: relative;
  width: auto;
  margin: 10px;
}

@media (min-width: 576px) {
  .modal-dialog {
    max-width: 500px;
    margin: 30px auto; /* для отображения модального окна по центру */
  }
}
/* свойства для блока, содержащего контент модального окна */
.modal-content {
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  background-color: #ffffff;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.3rem;
  outline: 0;
}

@media (min-width: 768px) {
  .modal-content {
    -webkit-box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  }
}
/* свойства для заголовка модального окна */
.modal-header {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 15px;
  border-bottom: 1px solid #eceeef;
}

.modal-title {
  color: #4F8FF0;
  font-weight: 900;
  margin-top: 0;
  margin-bottom: 0;
  line-height: 1.5;
  font-size: 1.25rem;
  font-weight: 500;
}

/* свойства для кнопки "Закрыть" */
.close {
  float: right;
  /* font-family: sans-serif; */
  font-size: 40px;
  font-weight: 300;
  line-height: 1;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  opacity: 0.5;
  text-decoration: none;
}

/* свойства для кнопки "Закрыть" при нахождении её в фокусе или наведении */
.close:focus, .close:hover {
  color: #000;
  text-decoration: none;
  cursor: pointer;
  opacity: 0.75;
}

/* свойства для блока, содержащего основное содержимое окна */
.modal-body {
  position: relative;
  -webkit-box-flex: 1;
  -webkit-flex: 1 1 auto;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  padding: 15px;
  overflow: auto;
}

.modal-input-name {
  width: 300px;
  height: 50px;
  border-bottom: 1px solid #4F8FF0;
}

.modal-input-tel {
  width: 300px;
  height: 50px;
  border-bottom: 1px solid #4F8FF0;
}

.modal-button {
  width: 300px;
  height: 50px;
  margin: 15px auto;
  transition: all 0.3s ease;
}

