.materials__discription {
  font-weight: 400;
  font-size: 16px;
  max-width: 920px;
  margin: 0 auto;
  margin-bottom: 48px;
}

.materials__wrapper {
  display: flex;
  gap: 24px;
  margin-bottom: 48px;
  overflow: scroll;
}

.material__card {
  text-align: left;
  flex: 1 1 30%;
}

.material__img-shell {
  margin-bottom: 24px;
  position: relative;
  cursor: pointer;
  min-width: 250px;
}

.material__img-shell > img {
  width: 100%;
}

.material__img-shell::before {
  content: "";
  background-color: hsla(216, 84%, 63%, 0.4);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.3s ease;
}

.material__img-shell:hover::before {
  opacity: 1;
}

.material__img-shell::after {
  content: url(../img/materials/eye.svg);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: all 1s ease;
}

.material__img-shell:hover::after {
  opacity: 1;
}

.materials__decoration {
  opacity: 0;
  transition: all 2s ease;
}

.materials__decoration:hover {
  opacity: 1;
}

.material__title-card {
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 16px;
  transition: all 0.3s ease;
}

.material__date {
  font-weight: 300;
  font-size: 12px;
  margin-bottom: 16px;
}

.material__discription-card {
  font-weight: 400;
  font-size: 14px;
  margin-bottom: 24px;
}

@media (max-width: 900px) {
  .materials {
    padding: 40px 0;
  }
}
/* footer -------------- */
.footer {
  background-color: var(--black-text);
  color: var(--color-white);
  padding-top: 80px;
}

.footer__top {
  display: flex;
  gap: 30px;
  justify-content: space-between;
  align-items: start;
  margin-bottom: 48px;
}

.footer__logo-white {
  text-align: start;
}

.footer__discription {
  font-weight: 400;
  font-size: 16px;
  max-width: 862px;
}

hr {
  border: 1px solid hsla(0, 0%, 55%, 0.2);
  margin-bottom: 48px;
}

.footer__bottom {
  display: flex;
  justify-content: space-between;
  margin-bottom: 80px;
}

.footer__contacts {
  display: flex;
  gap: 24px;
  flex-direction: column;
  flex: 1 1 30%;
  margin-right: 13%;
}

.img-frame {
  background-color: #2b2b2b;
  width: 55px;
  height: 55px;
  float: left;
  align-content: center;
  margin-right: 24px;
}

.footer__contact-naming {
  text-transform: uppercase;
  font-size: 12px;
  margin-top: 8.5px;
  text-align: left;
}

.footer__contact {
  font-size: 16px;
  line-height: 1.5;
  text-align: left;
}

.footer__nav {
  flex: 1 1 30%;
}

.footer__menu {
  display: flex;
  gap: 16px;
  flex-direction: column;
  align-items: start;
  text-transform: uppercase;
  font-size: 14px;
}

.footer__link {
  position: relative;
  white-space: nowrap;
  transition: all 0.3s ease-in-out;
}

.footer__link:hover {
  color: var(--color-blue);
}

.footer__link::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: var(--color-blue);
  bottom: 3px;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform 0.3s ease-in-out;
}

.footer__link:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}

.footer__supervisor {
  flex: 1 1 30%;
}

.footer__supervisor-avatar {
  float: left;
  width: 65px;
  height: 65px;
  border-radius: 50%;
  margin-right: 24px;
}

.footer__supervisor-name {
  font-size: 20px;
  line-height: 1.5;
  margin-top: 7px;
  margin-bottom: 4px;
  text-align: left;
}

.soopervisor-post {
  font-weight: 300;
  font-size: 14px;
  margin-bottom: 23px;
  text-align: left;
}

.footer__supervisor-discription {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5;
  text-align: left;
  margin-bottom: 24px;
}

.footer__button {
  display: block;
}

@media (max-width: 900px) {
  .footer__discription {
    display: none;
  }
  .footer__nav {
    display: none;
    gap: 40px;
  }
}
@media (max-width: 578px) {
  .footer {
    padding-top: 40px;
  }
  .footer__top {
    margin-bottom: 24px;
  }
  hr {
    margin-bottom: 24px;
  }
  .footer__bottom {
    flex-direction: column;
    margin-bottom: 40px;
  }
  .footer__contacts {
    margin-bottom: 40px;
  }
}
.personal-data {
  background-color: #181818;
}

.personal-data__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 400;
  font-size: 14px;
  max-width: 1320px;
  padding: 0 20px;
  height: 49px;
  margin: 0 auto;
  padding: 16px;
}

@media (max-width: 578px) {
  .personal-data__wrapper {
    flex-direction: column;
    height: 74px;
  }
}